<template>
	<view class="user-ticket">
		<view class="content boxsiz">
			<view class="head row bothSide verCenter">
				<view class="text column">
					<text class="t1">{{list.title}}[门票{{list.price}}元]</text>
					<text class="t2">{{list.start_time}}</text>
				</view>
				<view class="pic">
					<image :src="list.active_picture" mode="aspectFill"></image>
				</view>
			</view>
			<view class="qr-code">
				<image :src="list.qr_code" mode="aspectFill"></image>
				<text>门票时效性，过期作废</text>
			</view>
		</view>
		<view class="btn row bothSide">
			<!-- <button class="btn-1 boxsiz" open-type="share">立即赠送</button> -->
			<!-- <button class="btn-2 boxsiz">立即分享</button> -->
			<!-- <button class="btn-3 boxsiz" open-type="share" v-if="is_given==0&&status==0">立即赠送</button> -->
			<button class="btn-3 boxsiz" open-type="share" v-if="status==0" @click="give">立即赠送</button>
		    <button class="btn-4 boxsiz" v-if="status==1">已过期</button> 
		</view>
	</view>
</template>

<script>
	import {
		API
	} from '@/util/api.js';
export default {
	data() {
		return {
			tick_id:'',
			title:'',
			list:[],
			is_given:'',
			status:''
		};
	},
	onLoad(option) {
		this.tick_id=option.tick_id
		console.log(this.tick_id)
	},
	onShow() {
		this.getData()
	},
	onShareAppMessage(res) {
		   var article=this.list.title
		    return {
		      title: article,
		      path: '/pages/index/index?tick_id='+this.tick_id+'&title='+this.title
		    }
    },
	methods:{
		// 赠送门票前的状态
		give(){
		    	this.request(API.tickIsSend, 'post', {
		    	 	tick_id: this.tick_id
		    	}).then(res => {
		    	 	console.log(res)
		    	 })
		},
		// 获取门票数据
		getData(){
			this.request(API.tickDetail, 'get', {
				tick_id:this.tick_id
			}).then(res => {
				this.list=res.data
				this.title=this.list.title
				this.is_given=this.list.is_given
				this.status=this.list.status
				console.log(this.list)
			})
		}
	}
};
</script>

<style lang="scss">
@import '../../assets/css/ticket/electronTicket.scss';
</style>
